<!--驾驶员收入情况-->
<template>
	<div class="tw-template-wrapper">
		<el-form :inline="true" :model="query" size="small" class="tw-query-bar">
			<el-form-item>
				<el-autocomplete class="inline-input" v-model="query.certificate" placeholder="服务资格证号"
												 :fetch-suggestions="queryCertificateSearch" :trigger-on-focus="false"></el-autocomplete>
			</el-form-item>
			<el-form-item>
				<el-date-picker v-model="query.stime" type="datetime" placeholder="开始日期"></el-date-picker>
			</el-form-item>
			<el-form-item>
				<el-date-picker v-model="query.etime" type="datetime" placeholder="结束日期"></el-date-picker>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="handleQueryClick">查询</el-button>
				<el-button type="primary" @click="handleExportClick">导出</el-button>
			</el-form-item>
		</el-form>
		<div class="tw-query-panel">
			<el-table :data="filterTableList" v-loading="table.loading" border size="small" height="calc(100% - 42px)" style="width: 100%; margin-bottom: 10px;">
				<el-table-column type="index" label="序号" width="60"></el-table-column>
				<el-table-column prop="YINGYUN" label="服务资格证号" width="140"></el-table-column>
				<el-table-column prop="NAME" label="驾驶员姓名" width="140"></el-table-column>
				<el-table-column prop="YYCS" label="营运次数/次" width="180"></el-table-column>
				<el-table-column prop="JINE" label="营运金额/元" width="180"></el-table-column>
				<el-table-column prop="ZLC" label="总里程/公里" width="180"></el-table-column>
				<el-table-column prop="JICHENG" label="载客里程/公里" width="180"></el-table-column>
				<el-table-column prop="KONGSHI" label="空驶里程/公里" width="180"></el-table-column>
				<el-table-column prop="RATE" label="实载率" width="180"></el-table-column>
				<el-table-column prop="ZKSJ" label="载客时间/分钟" width="180"></el-table-column>
				<el-table-column prop="DHSJ" label="载客等候时间/分钟" width="180"></el-table-column>
			</el-table>
			<el-pagination background :page-size="table.pageSize" :current-page="table.currentPage" :total="table.total" layout="prev, pager, next, total" @current-change="handleTablePageCurrentChange"></el-pagination>
		</div>
	</div>
</template>

<script>
	import _ from 'underscore'
	import axios from 'axios'
	import moment from 'moment'
	import TWUnit from '../../components/TWTableColumn/TWUnit'
	import {formatCustomizeDate, formatDateTime} from '../../assets/js/util'

	export default {
		name: "DriverIncome",
		data() {
			return {
				query: {
					certificate: '',
					stime: '',
					etime: ''
				},
				lpNumberLoading: false,
				lpNumberOption: [],
				table: {
					loading: false,
					data: [
						{
							fwzg: '006521',
							jsym: '张三',
							gosi: '临安众安客运出租汽车有限公司',
							clis: 68,
							yyus: 43,
							cclv: '63.24%',
							yycs: 448,
							yyje: 6998,
							zlic: 3192,
							zklc: 1743,
							kslc: 1449,
							szlv: '54.60%',
							zksj: 4948,
							zkdhsj: 1990,
						}
					],
					pageSize: 20,
					currentPage: 1,
					total: 0
				}
			}
		},
		mounted() {
			this.$nextTick(() => {
				this.query.stime = formatCustomizeDate(moment(), 'YYYY-MM-DD 00:00:00');
				this.query.etime = formatCustomizeDate(moment(), 'YYYY-MM-DD 23:59:59');
				this.getIncomeAnalysis();
			});
		},
		computed: {
			filterTableList() {
				const {data, pageSize, currentPage} = this.table;
				const pageIndex = currentPage -1;
				return _.filter(data, (item, index) => {
					return  index >= pageIndex * pageSize && index < currentPage * pageSize;
				})
			}
		},
		methods: {
			/*数据接口*/
			getIncomeAnalysis(){
				this.table.loading = true;
				const {certificate, stime, etime} = this.query;
				axios.get('industryOperation/getIncomeAnalysis', {
					baseURL: this.baseURL,
					params: {
						cardNumber:certificate,
						stime: stime && moment(stime).format('YYYY-MM-DD HH:mm:ss'),
						etime: etime && moment(etime).format('YYYY-MM-DD HH:mm:ss')
					}
				}).then(res => {
					this.table.data = res.data || [];
					this.table.currentPage = 1;
					this.table.total = this.table.data.length;
					this.table.loading = false;
				})
			},
			queryCertificateSearch(query, cb) {
				cb(_.map(_.filter(['三全鲜食（北新泾店）', 'Hot honey 首尔炸鸡（仙霞路）', '新旺角茶餐厅',
					'泷千家(天山西路店)', '胖仙女纸杯蛋糕（上海凌空店）', '贡茶', '豪大大香鸡排超级奶爸',
					'茶芝兰（奶茶，手抓饼）', '十二泷町', '星移浓缩咖啡', '阿姨奶茶/豪大大', '新麦甜四季甜品炸鸡',
					'Monica摩托主题咖啡店', '浮生若茶（凌空soho店）', 'NONO JUICE  鲜榨果汁', 'CoCo都可(北新泾店）',
					'快乐柠檬（神州智慧店）', 'Merci Paul cafe', '猫山王（西郊百联店）', '枪会山', '纵食', '钱记',
					'壹杯加', '唦哇嘀咖', '爱茜茜里(西郊百联)', '爱茜茜里(近铁广场)', '鲜果榨汁', '开心丽果',
					'超级鸡车', '妙生活果园', '香宜度麻辣香锅', '凡仔汉堡', '港式小铺', '蜀香源麻辣香锅',
					'北京饺子馆', '饭典', '焦耳', '动力鸡车', '浏阳蒸菜', '四海游龙', '樱花食堂',
					'壹分米客家传统调制米粉', '福荣祥烧腊', '速记黄焖鸡米饭', '红辣椒麻辣烫', '(小杨生煎)西郊百联餐厅',
					'阳阳麻辣烫', '南拳妈妈龙虾盖浇饭'], item => item.indexOf(query) > -1), item => {
					return {label: item, value: item}
				}));
			},
			/*事件*/
			handleQueryClick() {
				this.table.currentPage = 1;
				this.getIncomeAnalysis();
			},
			handleExportClick() {
				const {certificate, stime, etime} = this.query;
				this.$confirm('是否需要导出?', '提示', {
					confirmButtonText: '是',
					cancelButtonText: '否',
					cancelButtonClass: 'el-button--danger',
					closeOnClickModal: false,
					type: 'info',
					center: true
				}).then(() => {
					window.open(`${this.baseURL}industryOperation/getIncomeAnalysisExcel?cardNumber=${certificate}&stime=${stime && moment(stime).format('YYYY-MM-DD HH:mm:ss')}&etime=${etime && moment(etime).format('YYYY-MM-DD HH:mm:ss')}`);
				}).catch(() => {
				});
			},
			handleTablePageCurrentChange(index) {
				this.table.currentPage = index;
			}
		},
		components: {
			'tw-unit': TWUnit
		}
	}
</script>

<style scoped>

